@import "../index.scss";

header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

@mixin gradient($layer, $opacity) {
  position: absolute;
  top: calc($layer * $gradient-height);
  left: 0;
  right: 0;
  height: $gradient-height;
  opacity: $opacity;
  background: linear-gradient(90deg, $color-gradient-1, $color-gradient-2);
}

// Header layers ..
.gradient-1 {
  @include gradient(0, 1);
}
.gradient-2 {
  @include gradient(1, 0.85);
}
.gradient-3 {
  @include gradient(2, 0.55);
}
.gradient-4 {
  @include gradient(3, 0.4);
}
.gradient-5 {
  @include gradient(4, 0.25);
}
.gradient-6 {
  @include gradient(5, 0.17);
}

//
.header-menu {
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-family: $font-lcd;
  font-size: $gradient-height;
  color: $color-bg-1;
  line-height: $gradient-height;

  text-align: center;
}

.header-links {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.link {
  width: fit-content;
}
